const RenderOption = ({ option, optionText, correctAnswer, checkAnswer, isShowCurrectAnswer }) => {
  const classText = (isShowCurrectAnswer && correctAnswer === option) ? 'card option bg-success' : 'card option';
  
  return (
    <div className="col-12 col-lg-6">
      <button className={classText} onClick={() => checkAnswer(option)}>
        <div className="card-body">
          <p><b>{ option }. </b> { optionText }</p>
        </div>
      </button>
    </div>
  );
};

const RenderQuestion = ({ question, checkAnswer, isShowCurrectAnswer, onSkipClick }) => {  
  return (
    <div className="row">
      <div className="col-12 mt-4 mb-3">
        <p className="question text-white"><b>Q. </b> { question.question }</p>
      </div>
      <RenderOption option='A' optionText={question.option1} correctAnswer={question.correct_answer} checkAnswer={checkAnswer} isShowCurrectAnswer={isShowCurrectAnswer} />
      <RenderOption option='B' optionText={question.option2} correctAnswer={question.correct_answer} checkAnswer={checkAnswer} isShowCurrectAnswer={isShowCurrectAnswer} />
      <RenderOption option='C' optionText={question.option3} correctAnswer={question.correct_answer} checkAnswer={checkAnswer} isShowCurrectAnswer={isShowCurrectAnswer} />
      <RenderOption option='D' optionText={question.option4} correctAnswer={question.correct_answer} checkAnswer={checkAnswer} isShowCurrectAnswer={isShowCurrectAnswer} />
      <div className="col-12 text-center my-4">
        <button className="btn btn-danger" onClick={onSkipClick}>Skip {'>>'}</button>
      </div>
    </div>
  );
};

const NoQuestion = () => (
  <h4 className="text-white text-center my-5">No question exists!</h4>
);

const RenderSuccessPopup = () => (
  <div className="popup d-flex justify-content-center align-items-center">
    <div className="text-center">
      <img src="/assets/images/face_throwing_a_kiss_256_1.gif" alt="success" width="110" />
      <h4 className="text-success my-3">Correct Answer!</h4>
    </div>
  </div>
);

const RenderWrongPopup = () => (
  <div className="popup d-flex justify-content-center align-items-center">
    <div className="text-center">
      <img src="/assets/images/loudly_crying_face_256_1.gif" alt="wrong" width="110" />
      <h3 className="text-wrong my-3">Wrong Answer!</h3>
      <p>Sare paise barbad ho gye.</p>
    </div>
  </div>
);

const customStyles = {
  content : {
    top: '50%',
    left: '50%',
    right: 'auto',
    bottom: 'auto',
    marginRight: '-50%',
    transform: 'translate(-50%, -50%)'
  }
};

const Home = () => {
  const [bgClass, setBgClass] = useState('');
  const [isSuccess, setIsSuccess] = useState(false);
  const [isWrong, setIsWrong] = useState(false);
  const [question, setQuestion] = useState(null);
  const [isShowCurrectAnswer, setIsShowCurrectAnswer] = useState(false);
  const [selectedAnswer, setSelectedAnswer] = useState(false);
  const [total, setTotal] = useState(0);
  const [solved, setSolved] = useState(0);
  const [correct, setCorrect] = useState(0);
  const [wrong, setWrong] = useState(0);
  const [isConfirmPopup, setIsConfirmPopup] = useState(false);

  const bgs = ['bg1', 'bg2', 'bg3', 'bg4', 'bg6', 'bg7', 'bg8', 'bg9', 'bg10', 'bg11', 'bg12'];

  const randomBg = () => {
    return bgs[Math.floor(Math.random() * bgs.length)];
  };

  const successPopup = () => {
    setIsSuccess(true);
    setTimeout(() => {
      setIsSuccess(false);
    }, 1000);
  };

  const wrongPopup = () => {
    setIsWrong(true);
    setTimeout(() => {
      setIsWrong(false);
    }, 1000);
  };

  const checkAnswer = (selectedAnswer) => {
    setIsConfirmPopup(true);
    setSelectedAnswer(selectedAnswer);
  };

  const confirmPopupClick = () => {
    setIsConfirmPopup(false);
    setIsShowCurrectAnswer(true);

    setTimeout(() => {
      setIsShowCurrectAnswer(false);
      if (selectedAnswer === question.correct_answer) {
        successPopup();
        db.collection("questions").doc(question.id).update({
          user_answer: selectedAnswer,
          user_answer_status: 1
        });
      } else {
        wrongPopup();
        db.collection("questions").doc(question.id).update({
          user_answer: selectedAnswer,
          user_answer_status: 2
        });
      }
      setSelectedAnswer('');
    }, 500);
  }

  const onSkipClick = () => {
    setIsShowCurrectAnswer(true);

    setTimeout(() => {
      setIsShowCurrectAnswer(false);
      db.collection("questions").doc(question.id).update({
        user_answer_status: 3
      });
    }, 1000);
  };

  useEffect(() => {
    let bg = randomBg();
    setBgClass(bg);

    db.collection("questions")
      .where('user_answer_status', '==', 0)
      .limit(1)
      .onSnapshot((doc) => {
        if (!doc.empty) {
          setQuestion({ id: doc.docs[0].id, ...doc.docs[0].data() });
        } else {
          setQuestion(null);
          console.log("No such document!");
        }
      });

    db.collection("questions")
      .onSnapshot((doc) => {
        setTotal(doc.size);
      });

    db.collection("questions")
      .where('user_answer_status', '>', 0)
      .onSnapshot((doc) => {
        setSolved(doc.size);
      });

    db.collection("questions")
      .where('user_answer_status', '==', 2)
      .onSnapshot((doc) => {
        setWrong(doc.size);
      });

    db.collection("questions")
      .where('user_answer_status', '==', 1)
      .onSnapshot((doc) => {
        setCorrect(doc.size);
      });
  }, []);

  return (
    <div className={`bg ${bgClass}`}>
      <div className="container">
        <div className="row">
          <div className="col-12 my-3">
            <h1 className="text-white text-center">Mintu App</h1>
          </div>
          <div className="col-12 col-md-6 col-lg-3">
            <p className="text-white"><b>Total: </b> { total }</p>
          </div>
          <div className="col-12 col-md-6 col-lg-3">
            <p className="text-white"><b>Solved: </b> { solved }</p>
          </div>
          <div className="col-12 col-md-6 col-lg-3">
            <p className="text-white"><b>Correct: </b> { correct }</p>
          </div>
          <div className="col-12 col-md-6 col-lg-3">
            <p className="text-white"><b>Wrong: </b> { wrong }</p>
          </div>
        </div>
        { question ? <RenderQuestion question={question} checkAnswer={checkAnswer} isShowCurrectAnswer={isShowCurrectAnswer} onSkipClick={onSkipClick} /> : <NoQuestion/> }
        { isSuccess && <RenderSuccessPopup/> }
        { isWrong && <RenderWrongPopup/> }
        {
          isConfirmPopup && (
            <Modal
              isOpen={true}
              style={customStyles}
            >
              <div className="text-center">
                <img src="/assets/images/warning-shield.png" alt="Popup" width="70" />
                <h5 className="my-4">Mintu lock kiya jaye?</h5>
                <button className="btn btn-success"  onClick={() => {
                  confirmPopupClick();
                }}>Yes</button>
                <button className="btn btn-danger ml-4" onClick={() => {
                  setIsConfirmPopup(false);
                }}>No</button>
              </div>
            </Modal>
          )
        }
      </div>
    </div>
  );
};

let q = [];

const Admin = () => {
  const [question, setQuestion] = useState('');
  const [option1, setOption1] = useState('');
  const [option2, setOption2] = useState('');
  const [option3, setOption3] = useState('');
  const [option4, setOption4] = useState('');
  const [correctAnswer, setCorrectAnswer] = useState('');

  const onAddClick = () => {
    db.collection('questions').add({
      correct_answer: correctAnswer,
      question: question,
      option1: option1,
      option2: option2,
      option3: option3,
      option4: option4,
      user_answer: "",
      user_answer_status: 0
    })
    .then((k) => {
      alert('added');
      setQuestion('');
      setOption1('');
      setOption2('');
      setOption3('');
      setOption4('');
      setCorrectAnswer('');
    })
    .catch(console.log)
  };

  const onAddBulkClick = () => {
    q.forEach((d) => {
      db.collection('questions').add({
        correct_answer: d.correct_answer,
        question: d.question,
        option1: d.option1,
        option2: d.option2,
        option3: d.option3,
        option4: d.option4,
        user_answer: d.user_answer,
        user_answer_status: d.user_answer_status,
        help: d.help
      })
      .then((k) => {
        console.log('added');
      })
      .catch(console.log)
    })
  }

  return (
    <div className="container">
      <div className="row">
        <div className="col-12">
          <div className="form-group">
            <label>Question</label>
            <textarea rows="6" className="form-control" value={question} onChange={e => {
              setQuestion(e.target.value)
            }}></textarea>
          </div>
        </div>
        <div className="col-12 col-lg-6">
          <div className="form-group">
            <label>A</label>
            <textarea className="form-control" value={option1} onChange={e => {
              setOption1(e.target.value)
            }}></textarea>
          </div>
        </div>
        <div className="col-12 col-lg-6">
          <div className="form-group">
            <label>B</label>
            <textarea className="form-control" value={option2} onChange={e => {
              setOption2(e.target.value)
            }}></textarea>
          </div>
        </div>
        <div className="col-12 col-lg-6">
          <div className="form-group">
            <label>C</label>
            <textarea className="form-control" value={option3} onChange={e => {
              setOption3(e.target.value)
            }}></textarea>
          </div>
        </div>
        <div className="col-12 col-lg-6">
          <div className="form-group">
            <label>D</label>
            <textarea className="form-control" value={option4} onChange={e => {
              setOption4(e.target.value)
            }}></textarea>
          </div>
        </div>
        <div className="col-12 col-lg-6">
          <div className="form-group">
            <label>Currect Answer</label>
            <select className="form-control" value={correctAnswer} onChange={e => {
              setCorrectAnswer(e.target.value)
            }}>
              <option>A</option>
              <option>B</option>
              <option>C</option>
              <option>D</option>
            </select>
          </div>
        </div>
        <div className="col-12 col-lg-6">
          <div className="form-group">
            <button className="btn btn-success" onClick={onAddClick}>Add</button>
            <button className="btn btn-info ml-3" onClick={onAddBulkClick}>Add Bulk</button>
          </div>
        </div>
      </div>
    </div>
  );
}

const App = () => {
  return (
    <>
    {/* <Admin/> */}
    <Home />
    </>
  );
}

